<template>
	<div id="userInfo">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">个人信息</div>
        <div class="van-nav-bar__right" @click="submitUserInfo">
          保存
        </div>
      </div>
    </div>
    <div class="body_box">
      <div class="background_color_ffffff text_center" style="padding: 10px">
        <img :src="userImg||'static/img/img_ghtx_grxx.png'" style="position: inherit;left: 50%;width: 80px;" @click="uploadImg"/>
      </div>
      <div class="background_color_ffffff" style="padding: 15px;margin-bottom: 5px">
        <img src="static/img/icon_dianyuan--mf.png" style="vertical-align: middle;">
        <input type="text" placeholder="请输入修改后的用户名" class="font_12 userName" v-model="userName"/>
      </div>
      <van-cell-group class="user-group" style="margin: 5px 0;">
        <van-cell is-link @click="toChangePwd">
          <img src="static/img/icon_mima_grxx1.png" class="barImg"/>
          <span class="padding-left-5">密码修改</span>
        </van-cell>
      </van-cell-group>
      <van-cell-group class="user-group" style="margin: 5px 0;">
        <van-cell is-link @click="toChangePhone">
          <img src="static/img/icon_shouji_grxx1.png" class="barImg"/>
          <span class="padding-left-5">手机号修改</span>
        </van-cell>
      </van-cell-group>
    </div>
	</div>
</template>

<script>
	export default {
		name: 'userInfo',
		data() {
			return {
			  userImg:'',
			  userName:'',
        userId:localStorage.getItem('userId')
			}
		},
    mounted() {
      this.getUserInfo();
    },
		methods: {
      uploadImg(){
        let $this = this;
        dsBridge.call('uploadHeadImg');
        dsBridge.register('addHeadImgCallback', function(rpdata) {
          $this.userImg = rpdata;
        })
      },
      submitUserInfo(){
        let $this = this;
        let obj = {
          userid: this.userId,
          username: this.userName,
          userimg: this.userImg,
        };
        let qs = require('qs');
        $this.$ajax.post("user/updateUser", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.getUserInfo();
            $this.$toast({
              message: '保存成功!',
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      getUserInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("user/getuserinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.userName = rpdata.data.data.username;
            $this.userImg = rpdata.data.data.userimg;
          }
        })
        .catch(function(err) {})
      },
      toChangePwd(){
        this.$router.push('changePwd');
      },
      toChangePhone(){
        this.$router.push('changePhone');
      }
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .body_box{
    margin-top: 50px;
  }
  .barImg {
    width: 20px;
    vertical-align: middle;
  }
  .userName{
    margin-left: 10px;
    border: 0;
  }
</style>



